.App {
  text-align: center;
}

.map{
  margin: auto;
  width: calc(100% - 40px);
  height: 900px;
  overflow: hidden;
  border: 10px solid #fff;
  box-shadow: 0 3px 30px rgb(0 0 0 / 20%);
  border-radius: 20px;
  background: #fff;
}
.btnWrap button{
  display: block;
  margin: 0 0 10px;
}
.btnSearch ,.btnZoom{
  background: #fff;
  line-height: 40px;
  border: none;
  border-radius: 5px;
  margin: 0 5px;
  box-shadow: 0 3px 10px rgb(0 0 0 / 20%);
  width: 40px;
  font-family: "Montserrat",sans-serif;
  font-weight: 400;
  font-size: 30px;
  color: #81b49e;
  padding: 0;
}
.btnWrap{
  position: absolute;
  top: 20px;
  left: 30px;
  z-index: 10;
}
.btnSearch {
  height: 40px;
}
.btnSearch .iconSearch {
  width: 40px;
  height: 40px;
  background: url('https://cardanovillage.io/static/media/iconSearch.c9bd36c8.svg') 50% no-repeat;
  background-size: 26px;
  display: inline-block;
}


#terrain {
  width: 100%;
}

/* The Modal (background) */
.mi-modal {
  margin: 20px;
  position: fixed; /* Stay in place */
  z-index: 1; /* Sit on top */
  right: 0;
  top: 30px;
  width: 20%; /* Full width */
  overflow: auto; /* Enable scroll if needed */
  background-color: rgb(0,0,0); /* Fallback color */
  background-color: rgba(0,0,0,0.4); /* Black w/ opacity */
  -webkit-animation-name: fadeIn; /* Fade in the background */
  -webkit-animation-duration: 0.4s;
  animation-name: fadeIn;
  animation-duration: 0.4s
}

/* Modal Content */
.mi-modal-content {
  position: fixed;
  /* top: 0; */
  background-color: black;
  -webkit-animation-name: slideIn;
  -webkit-animation-duration: 0.4s;
  animation-name: slideIn;
  animation-duration: 0.4s
}

/* The Close Button */
.mi-close {
  color: white;
  float: right;
  font-size: 28px;
  font-weight: bold;
}

.mi-close:hover,
.mi-close:focus {
  color: #fffa;
  text-decoration: none;
  cursor: pointer;
}

.mi-modal-header {
  padding: 20px;
  color: white;
}

.mi-modal-body {margin: 20px;}

.terrain-content {
  color: white;
  text-align: left;
}

.status-comment {
  position: relative;
  padding-left: 25px;
  width: 90%;
}

.buy-button {
  text-align: center;
}

.buy-button button {
  font-size: 20px;
  padding: 3px 20px;
  border: none;
  border-radius: 4px;
}

/* Add Animation */
@-webkit-keyframes slideIn {
  from {top: 0px; opacity: 0} 
  to {top: 50px; opacity: 1}
}

@keyframes slideIn {
  from {top: 0px; opacity: 0}
  to {top: 50px; opacity: 1}
}

@-webkit-keyframes fadeIn {
  from {opacity: 0} 
  to {opacity: 1}
}

@keyframes fadeIn {
  from {opacity: 0} 
  to {opacity: 1}
}

.popupWrap {
  display: block;
  min-width: 280px;
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 200;
}
.popupWrap .popup2 {
  max-width: 300px;
}
.popupWrap .popupArea {
  width: 640px;
  max-height: 80vh;
  position: fixed;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%,-50%);
  transform: translate(-50%,-50%);
  background: #fff;
  border-radius: 20px;
  z-index: 20;
}
.popupWrap .btnClose {
  width: 45px;
  height: 45px;
  background: url("https://cardanovillage.io/static/media/iconCloseB.62995f00.svg") #fff 50% no-repeat;
  border-radius: 50%;
  position: absolute;
  right: -100px;
  top: -10px;
  z-index: 500;
  box-shadow: 0 3px 15px rgb(0 0 0 / 15%);
}
.popupWrap .popup2 .popupContents {
  overflow: hidden;
  background-color: white;
  padding: 40px
}

.popupWrap .popupContents {
  padding: 40px 60px;
  overflow: scroll;
  text-align: center;
  width: 100%;
  max-height: 80vh;
}
.popupWrap .popupTitle, .popupWrap .popupTitle2 {
    font-family: "Montserrat",sans-serif;
    font-weight: 700;
    width: 100%;
}
.popupWrap .popupTitle {
  font-size: 30px;
  color: #81b49e;
  text-align: center;
}
.popupWrap .popupText2 {
  width: 100%;
  display: inline-block;
  font-size: 0;
}
.popupWrap .popupText2 .row {
  width: 100%;
  text-align: right;
  margin-top: 20px;
  font-size: 0;
  position: relative;
}
.popupWrap .inputTitle {
  font-size: 14px;
  color: #222;
  font-family: "Noto Sans",sans-serif;
  font-weight: 700;
  text-align: left;
  width: 100%;
  display: inline-block;
}

.popupWrap .popupText2 input[type=password], .popupWrap .popupText2 input[type=text] {
  background: #fff;
  margin-top: 6px;
  border: 1px solid #dbdbdb;
  outline: none;
  -webkit-appearance: none;
  -webkit-border-radius: 0;
  transition: all .15s ease-in-out;
  outline: 0 solid #81b49e;
  font-size: 16px;
  line-height: 41px;
  width: 100%;
  padding: 0 10px;
  border-radius: 5px;
  font-weight: 400;
  color: #222;
  font-family: "Noto Sans",sans-serif;
}
.popupWrap .popupText2 .btnLOGIN {
  width: 100%;
  font-family: "Montserrat",sans-serif;
  background: #81b49e;
  font-size: 16px;
  line-height: 45px;
  border-radius: 5px;
  color: #fff;
  font-weight: 700;
  transition: all .15s ease-in-out;
  margin-top: 10px;
}